*, ::after, ::before {
    box-sizing: border-box;
}
body {
    font-family: 'Arimo', sans-serif;
    letter-spacing: -.03em;
    font-size: 16px;
    margin: 0 auto;
    background-color: #1d1d1d;
    -webkit-font-smoothing: antialiased;
    color: #fff;
}

/**
   * Header styles
   */

.nav-bar {
    margin-right: auto;
    margin-left: auto;
    width: 100%;
    top: 0;
    position: fixed;
    z-index: 8;
    background: #1d1d1d;
    overflow: hidden;
    color: #919191;
    border-bottom: 1px solid #2f2f2f;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    height: 60px;
    display: flex;
    align-items: center;
    padding: 0 15px;
}

.nav-bar-container {
    max-width: 1170px;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav-logo {
    color: #6ac045;
    font-weight: 500;
    font-size: 32px;
}

.nav-search {
    position: relative;
}

.nav-search>img {
    position: absolute;
    left: 10px;
    top: 50%;
    -webkit-transform: translate(0, -50%);
    transform: translate(0, -50%);
}

.nav-search-input {
    width: 400px;
    height: 35px;
    font-size: .85em;
    font-weight: bold;
    color: #919191;
    border: 2px solid #5a5a5a;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    padding: 4px 30px 4px 40px;
    background-color: #1d1d1d;
}

.nav-search-input:focus {
    outline: none;
    border-color: #969696;
    color: #fff;
}

/**
   * Main content style
   */

.main-content {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    grid-gap: 56px;
    position: relative;
    top: 120px;
    max-width: 1170px;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 8rem;
}

.movie {
    width: 100%;
    max-width: 250px;
}

.movie-logo {
    width: 150px;
    display: block;
    margin: 6rem auto 0;
}

.movie:hover .movie-item {
    border-color: #6ac045;
    cursor: pointer;
}

.movie:hover .movie-item>img {
    opacity: .5;
}

.movie-item {
    display: inline-block;
    border: 5px solid #fff;
    transition: border .15s ease-in-out;
    border-radius: 4px;
}

.movie-item>img {
    display: block;
    max-width: 100%;
    transition: opacity .15s ease-in-out;
    width: 240px;
    height: 358px;
    object-fit: cover;
}

.movie-item-title {
    margin: 0;
    font-size: .95em;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.movie-item-sub-title {
    font-size: .85em;
    color: #919191;
}

/**
   * Modal content
   */

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .7);
    z-index: 1000;
}

.close-modal {
    position: absolute;
    right: -10px;
    top: -10px;
    text-align: center;
    width: 30px;
    height: 30px;
    display: flex;
    text-decoration: none;
    vertical-align: middle;
    cursor: pointer;
    background-color: #6ac045;
    border: 3px solid #ffffff;
    border-radius: 100%;
    align-items: center;
    justify-content: center;
}

.close-modal>img {
    width: 24px;
    height: auto;
}

.modal-container {
    width: 100%;
    max-width: 50%;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: #FFF;
    padding: 30px;
    z-index: 1000;
    color: #333;
    border-radius: 5px;
}

.modal-body {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.modal-body>img {
    display: block;
    max-width: 100%;
    margin-right: 20px;
}

.movie-info {
    color: black;
    font-weight: 600;
    margin: 10px 0px;
    line-height: 1.5;
    overflow: hidden;
    text-overflow: ellipsis;
}

.movie-info>span {
    color: black;
    font-weight: 400;
}

.movie-title {
    margin-top: 0;
    border-bottom: 2px solid #6ac045;
    padding-bottom: 10px;
    font-size: 14px;
    font-weight: 500;
    color: #333;
}

.movie-title,
.movie-title>span {
    line-height: 1.1;
}

.movie-title>span {
    font-size: 30px;
    display: block;
    font-weight: 700;
}


/**
   * Media queries / Responsive
   */

@media only screen and (max-width: 1280px) {
    .modal-container {
        max-width: 80%;
    }
}

@media only screen and (max-width: 1160px) {
    .main-content {
        justify-content: center;
    }

    .movie {
        max-width: 20%;
    }

    .movie-item>img {
        height: auto;
    }
}

@media only screen and (max-width: 767px) {
    .nav-bar {
        height: auto;
        padding: 5px 15px 10px;
    }

    .nav-bar-container {
        flex-direction: column;
    }

    .nav-search {
        width: 100%;
    }

    .nav-search-input {
        width: 100%;
    }

    .nav-logo {
        margin-bottom: 10px;
    }

    .main-content {
        width: 100%;
        padding: 0 15px 8rem;
        grid-gap: 15px;
        justify-content: space-around;
    }

    .movie {
        max-width: 47%;
    }

    .movie-item>img {
        width: 100%;
        height: auto;
    }

    .modal-container {
        max-width: 100%;
        border-radius: 0;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        -webkit-transform: inherit;
                transform: inherit;
        padding: 15px;
        overflow: auto;
    }

    .close-modal {
        top: 0;
        right: 0;
        border-radius: inherit;
        border-color: #6ac045;
    }

    .modal-body {
        flex-direction: column;
    }

    .modal-body>img {
        height: 270px;
        object-fit: contain;
        margin: 0 auto 20px;
    }
}
